import store from 'store'
import moment from 'moment'
import { update } from 'ramda'
import { Mutation, MutationOptions } from '@/constants/types'
import { useReportList } from '@/data/useReport'
import { uuid } from '@/utils/webHelper'

export const useAddNewReport = ({ onCompleted }: MutationOptions = {}) => {
  const [reportList, setReportList] = useReportList()
  const handler = (values: any) => {
    let id = uuid()
    let newList = [
      ...reportList,
      {
        ...values,
        id,
        createdAt: moment(),
        createdAtStr: moment().format('YYYY/MM/DD HH:mm'),
        updatedAt: moment(),
        updatedAtStr: moment().format('YYYY/MM/DD HH:mm'),
      },
    ]

    setReportList(newList)
    store.set('reportListState', newList)

    if (onCompleted) {
      onCompleted({ ...values, id })
    }
  }
  return [false, handler] as Mutation
}

export const useUpdateReport = ({ onCompleted }: MutationOptions = {}) => {
  const [reportList, setReportList] = useReportList()

  const handler = (values: any) => {
    let index = reportList.findIndex((x: any) => `${x.id}` === `${values.id}`)
    let newList = update(
      index,
      {
        ...values,
        updatedAt: moment(),
        updatedAtStr: moment().format('YYYY/MM/DD HH:mm'),
      },
      reportList
    )
    setReportList(newList)
    store.set('reportListState', newList)

    if (onCompleted) {
      onCompleted()
    }
  }
  return [false, handler] as Mutation
}
